<template>
  <div class="registration">
    <div class="registration-block">
      <div class="registration-block__first">
        <div class="registration__title">Заполните необходимые данные</div>
        <div class="registration-step">
          <p class="registration-step__title">1 Шаг регистрации</p>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 50 50"
            class="registration-step__svg"
          >
            <path
              fill="#231F20"
              d="M15.563 40.836a.997.997 0 0 0 1.414 0l15-15a.999.999 0 0 0 0-1.414l-15-15a.999.999 0 1 0-1.414 1.414l14.293 14.293-14.293 14.293a.999.999 0 0 0 0 1.414z"
            />
          </svg>
        </div>

        <div class="registration__wrap-select">
          <div class="select-block__item">
            <div class="select-block__item-block">
              <label for="standard-select"> Выберите вид соревнований </label>
              <div class="select">
                <select required id="standard-select" v-model="getCompetition">
                  <option
                    v-for="comp in this.competition"
                    :key="comp.id"
                    :value="`${comp.id}`"
                  >
                    {{ comp.name }}
                  </option>
                </select>
                <span class="focus"></span>
              </div>
            </div>
          </div>
          <div class="info__input">
            <label for="art">ФИО Судьи</label>
            <input
              type="text"
              id="item"
              name="art"
              placeholder="Иванов Иван"
              v-model="fio"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
          <div class="info__input">
            <label for="art">Клуб</label>
            <input
              type="text"
              id="item"
              name="art"
              placeholder="Энергия"
              v-model="club"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
        </div>
        <div class="registration__wrap-select">
          <div class="select-block__item-block">
            <label for="standard-select">Выберите № Бригады </label>
            <div class="select">
              <select required id="standard-select" v-model="selectBrigade">
                <option value="1">Бригада №1</option>
                <option value="2">Бригада №2</option>
              </select>
              <span class="focus"></span>
            </div>
          </div>

          <div class="select-block__item-block">
            <label for="standard-select">Выберите Должность</label>
            <div class="select">
              <select required id="standard-select" v-model="selectPosition">
                <option value="Исполнение">Исполнение</option>
                <option value="Артистичность">Артистичность</option>
                <option value="Сложность">Сложность</option>
                <option value="Арбитр">Арбитр</option>
                <option value="Главный судья">Главный судья</option>
                <option value="Администратор">
                  Администратор соревнований
                </option>
              </select>
              <span class="focus"></span>
            </div>
          </div>
          <div class="select-block__item-block">
            <label for="standard-select">Выберите № Судьи</label>
            <div class="select">
              <select
                required
                id="standard-select"
                v-model="selectPositionNumber"
              >
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
              </select>
              <span class="focus"></span>
            </div>
          </div>
        </div>
        <ListJuj />
      </div>

      <div class="registration-block__second">
        <div class="registration__title">Зарегистрируйте судью</div>
        <div class="registration-step">
          <p class="registration-step__title">2 Шаг регистрации</p>
        </div>

        <div class="registration-block-form">
          <fieldset class="form-group">
            <label for="username">Задайте логин</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              name="username"
              v-model="username"
            />
          </fieldset>
          <fieldset class="form-group">
            <label for="username">Задайте пароль</label>
            <input
              type="password"
              class="form-control"
              name="password"
              placeholder="******"
              v-model="password"
            />
          </fieldset>
          <input
            type="button"
            value="Регистрация"
            class="button btn-login"
            @click="onSubmit()"
            :disabled="!this.username && !this.password"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { actionTypes } from "@/store/modules/auth";
import { actionTypesJudgind } from "@/store/modules/judging";
import { mapState } from "vuex";
import ListJuj from "../components/ListJuj.vue";
export default {
  name: "AppRegistration",
  data() {
    return {
      fio: "",
      club: "",
      selectBrigade: "",
      selectPosition: "",
      selectPositionNumber: "",
      username: "",
      password: "",
      role: "",
      getCompetition: "",
    };
  },
  components: { ListJuj },
  computed: {
    ...mapState({
      competition: (state) => state.judging.competition,
    }),
  },
  methods: {
    onSubmit() {
      if (this.selectPosition === "Исполнение") {
        this.role = "ROLE_EXE";
      } else if (this.selectPosition === "Артистичность") {
        this.role = "ROLE_ART";
        // this.role = "USER";
      } else if (this.selectPosition === "Сложность") {
        this.role = "ROLE_DIF";
      } else if (this.selectPosition === "Арбитр") {
        this.role = "ROLE_ARB";
      } else if (this.selectPosition === "Главный судья") {
        this.role = "ROLE_MAIN";
      } else {
        this.role = "ADMIN";
      }
      this.$store
        .dispatch(actionTypes.registration, {
          fio: this.fio,
          club: this.club,
          brigade: this.selectBrigade,
          position: this.selectPosition,
          positionNumber: this.selectPositionNumber,
          username: this.username,
          password: this.password,
          competition: Number(this.getCompetition),
          role: this.role,
        })
        .then((resp) => {
          this.$store
            .dispatch(actionTypesJudgind.getAllUsers)
            .then(() => {})
            .catch((er) => {
              console.log("Ошибка", er);
            });
          console.log(resp);
          this.fio = "";
          this.club = "";
          this.selectBrigade = "";
          this.selectPosition = "";
          this.selectPositionNumber = "";
          // this.competition = "";
          this.username = "";
          this.password = "";
          this.getCompetition = "";

          // if (!this.validationErrors) {
          //   this.$router.push({ name: "Home" });
          // }
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
  },
  mounted() {
    this.$store
      .dispatch(actionTypesJudgind.getCompetition)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
    this.$store
      .dispatch(actionTypesJudgind.getAllUsers)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>

