<template>
  <div class="score">
    <div class="wrap-loader" v-if="isLoading">
      <div id="loader"></div>
    </div>
    <template v-if="players">
      <div class="table-score" v-if="this.infoUser.role === 'ROLE_DIF'">
        <div class="table-score__judge">
          бригада № {{ this.infoUser.brigade }} {{ this.infoUser.position }} №
          {{ this.infoUser.positionNumber }}
        </div>
        <div class="table-score__number">
          № <span>{{ this.players.startId }}</span>
        </div>
        <div class="table-score__wrap">
          <div class="table-score__nomination">
            Номинация: <span>{{ this.players.nomination }}</span>
          </div>
          <div class="table-score__age">
            Категория: <span> {{ this.players.age }}</span>
          </div>
        </div>
        <div class="table-score__wrap2">
          <div
            class="table-score__fio"
            v-for="(sportsmen, index) in this.players.fio"
            :key="index"
          >
            {{ sportsmen }}
          </div>
        </div>
        <div class="info__input">
          <label for="exe">Оценка:</label>
          <input
            type="number"
            id="item"
            name="art"
            max="10"
            placeholder="10.0"
            v-model="scoreDif"
            required
            aria-label="Вводите"
            class="input-search"
          />
        </div>

        <p class="table-score__title">Сбавка:</p>

        <div class="info__input-dif">
          <div class="info__wrap">
            <!-- <label for="elements">Элементы</label> -->
            <input
              type="number"
              id="item"
              name="elements"
              max="10"
              placeholder="10.0"
              v-model="difDeduction"
              aria-label="Вводите"
              class="input-search"
            />
          </div>

          <!-- <div class="info__wrap">
          <label for="line">Линия</label>
          <input
            type="number"
            id="item"
            name="line"
            max="10"
            placeholder="10.0"
            v-model="line"
            aria-label="Вводите"
            class="input-search"
          />
        </div> -->
        </div>
        <div class="info__score-submit" v-if="isSubmitScore === true">
          <p>Оценка отправлена,ожидайте следующего номера</p>
        </div>
        <div class="info-score__btn">
          <input
            type="submit"
            value="Отправить"
            class="button btn-primary"
            @click="onSubmit()"
            :disabled="!this.scoreDif"
          />
        </div>
      </div>
    </template>
    <template v-else>
      <div class="table-score" v-if="this.infoUser.role === 'ROLE_DIF'">
        <div class="info__await">
          <p>Ожидайте начала соревнований!</p>
        </div>
      </div>
      <div class="table-score__error" v-else>
        <p class="error-info">Извините, данная страница для вас недоступна!</p>
        <input
          type="submit"
          value="На главную"
          class="button btn-primary"
          @click="backRoute()"
        />
      </div>
    </template>
    <!-- <div class="table-score__error" v-else>
      <p class="error-info">Извините, данная страница для вас недоступна!</p>
      <input
        type="submit"
        value="На главную"
        class="button btn-primary"
        @click="backRoute()"
      />
    </div> -->
  </div>
</template>

<script>
// import { actionTypesJudgind } from "@/store/modules/judging";
import { actionTypesSocket } from "@/store/modules/socket";
import { mapState } from "vuex";
export default {
  name: "AppDif",
  data() {
    return {
      scoreDif: "",
      isSubmitScore: false,
      // elements: "",
      difDeduction: "",
    };
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      players: (state) => state.socket.players,
      nowCompetition: (state) => state.judging.nowCompetition,
      isLoading: (state) => state.judging.isLoading,
    }),
  },
  methods: {
    onSubmit() {
      // this.players.fio.forEach((item) => this.idSportsmen.push(item.id));
      this.infoScore = {
        competition: this.nowCompetition,
        startId: this.players.startId,
        idJudge: this.infoUser.id,
        score: this.scoreDif,
        brigade: this.infoUser.brigade,
        difDeduction: this.difDeduction,
        idPerson: this.players.id,
      };
      this.$store
        .dispatch(actionTypesSocket.socketSendMessageDif, this.infoScore)
        .then(() => {
          this.scoreDif = false;
          this.difDeduction = "";
          this.isSubmitScore = true;
        })
        .catch((err) => {
          console.log(err);
        });
      // this.$store
      //   .dispatch(actionTypesJudgind.setResult, this.infoScore)
      //   .then(() => {
      //     this.score = false;
      //     this.isSubmitScore = true;
      //     this.idSportsmen = [];
      //   })
      //   .catch((error) => {
      //     console.log(error);
      //   });
    },
    backRoute() {
      this.$router.push({ name: "Index" });
    },
  },
  mounted() {
    this.$store
      .dispatch(actionTypesSocket.soketJujConnect, this.infoUser)
      .then(() => {
        this.$store.dispatch(
          actionTypesSocket.socketSendMessageActiveUser,
          this.infoUser.id
        );
      })
      .catch((er) => {
        this.$store.dispatch(actionTypesSocket.soketJujConnect, this.infoUser);
        console.log(er, "ошибка");
      });
  },
};
</script>

