export const mutationTypes = {
  SOCKET_ONOPEN: "✅ Socket connected!",
  SOCKET_ONCLOSE: "❌ Socket disconnected!",
  SOCKET_ONCLOSE_ARB: "❌ Socket disconnected arbitr!",
  SOCKET_ONERROR: "❌ Socket Error!!!",
  SOCKET_SENDMESSAGE: "Socket Send Message",
  SOCKET_ONMESSAGE_ARBITR: "Websocket message received arbitr",
  // SOCKET_ONMESSAGE_DIMMER: "Websocket message received dimmer",
  SOCKET_RECONNECT: "Websocket reconnected",
  SOCKET_RECONNECT_ERROR: "Websocket is having issues reconnecting..",
  SOCKET_SENDMESSAGE_JUDGIS: "Send message",
  SOCKET_SENDMESSAGE_JUDGIS_DIF: "Send message",
  SOCKET_SENDMESSAGE_ACTIVE_JUDGIS: "Active Room",
  SOCKET_SPORTSMEN_JUDGINGS: "Send sportsmen",
  SOCKET_SAVE_ID_USER_JUJ: "Attempt connection user juj",
  SOCKET_SAVE_ID_USER_ARB: "Attempt connection user arb",
};
export const actionTypesSocket = {
  socketConnect: "[socket] socketConnect",
  socketSendMessage: "[socket] socketSendMessage",
  socketSendMessageDif: "[socket] socketSendMessageDif",
  soketJujConnect: "[socket] socketJujConnect",
  socketSendMessageActiveUser: "[socket] socketSendMessageActiveUser",
};

const mutations = {
  [mutationTypes.SOCKET_ONMESSAGE_ARBITR](state, messageArb) {
    console.log("ПОЛУЧИЛ СООБЩЕНИЕ ДЛЯ АРБИТРА", messageArb);
    console.log(judging.state.allResults, "объект резалта до");
    let newElem = judging.state.allResults.find((element) => {
      return element.id === messageArb.id;
    });
    console.log(newElem, "Найденный элемент");
    var indexNewElem = judging.state.allResults.indexOf(newElem);
    if (messageArb.executionAllResponse !== null) {
      judging.state.allResults[indexNewElem].executionAllResponse =
        messageArb.executionAllResponse;
    }
    if (messageArb.artistryAllResponse !== null) {
      judging.state.allResults[indexNewElem].artistryAllResponse =
        messageArb.artistryAllResponse;
    }
    if (messageArb.difficultyAllResponse !== null) {
      judging.state.allResults[indexNewElem].difficultyAllResponse =
        messageArb.difficultyAllResponse;
    }
    if (messageArb.deductionAllResponse !== null) {
      judging.state.allResults[indexNewElem].deductionAllResponse =
        messageArb.deductionAllResponse;
    }
    state.messageArbitr = messageArb;
  },

  [mutationTypes.SOCKET_ONOPEN](state) {
    state.isConnected = true;
    console.log("✅ Socket connected!");
  },
  [mutationTypes.SOCKET_SENDMESSAGE_JUDGIS](state) {
    state.sendMessage = true;
    console.log("Send message!");
  },
  [mutationTypes.SOCKET_SENDMESSAGE_JUDGIS_DIF](state) {
    state.sendMessageDif = true;
    console.log("Send message dif!");
  },
  [mutationTypes.SOCKET_SAVE_ID_USER_JUJ](state, user) {
    state.infoUserConnect = user;
  },
  [mutationTypes.SOCKET_SAVE_ID_USER_ARB](state, user) {
    state.infoUserArbConnect = user;
  },
  [mutationTypes.SOCKET_SENDMESSAGE_ACTIVE_JUDGIS](state, userId) {
    state.userConnectID = userId;
    console.log("Пользователь активен");
  },
  [mutationTypes.SOCKET_ONCLOSE](state, event) {
    state.isConnected = false;
    state.stompClient = null;
    console.log(event, "❌ Socket disconnected!");
    state.recInterval = window.setInterval(function () {
      new_conn();
    }, 5000);
  },
  [mutationTypes.SOCKET_ONCLOSE_ARB](state, event) {
    state.isConnected = false;
    state.stompClient = null;
    console.log(event, "❌ Socket disconnected!");
    state.recInterval = window.setInterval(function () {
      new_conn_arb();
    }, 5000);
  },
  [mutationTypes.SOCKET_ONERROR](event) {
    state.isConnected = false;
    console.log(event);
  },
  // mutations for reconnect methods
  [mutationTypes.SOCKET_RECONNECT](state, count) {
    console.info(state, count);
  },
  [mutationTypes.SOCKET_RECONNECT_ERROR](state) {
    state.socket.reconnectError = true;
  },
  [mutationTypes.SOCKET_SPORTSMEN_JUDGINGS](state, players) {
    state.players = players;
  },
};
const actions = {
  [actionTypesSocket.socketConnect](context, user) {
    return new Promise((resolve) => {
      if (state.isConnected) return;
      console.log("Начал попытку соединения...", user);
      context.commit(mutationTypes.SOCKET_SAVE_ID_USER_ARB, user);

      state.stompClient = Stomp.over(state.socket, {
        debug: false,
      });

      state.stompClient.connect(
        {},
        (frame) => {
          context.commit(mutationTypes.SOCKET_ONOPEN);
          if (user.brigade === 1) {
            state.stompClient.subscribe("/topic/arbitrator_one", (tick) => {
              console.log("Подписка арбитра первой бригады");
              const itemMessageArbitr = JSON.parse(tick.body);
              context.commit(
                mutationTypes.SOCKET_ONMESSAGE_ARBITR,
                itemMessageArbitr
              );
            });
          } else {
            state.stompClient.subscribe("/topic/arbitrator_two", (tick) => {
              console.log("Подписка арбитра второй бригады");
              const itemMessageArbitr = JSON.parse(tick.body);
              context.commit(
                mutationTypes.SOCKET_ONMESSAGE_ARBITR,
                itemMessageArbitr
              );
            });
          }
          //Подписка на сообщения для арбитра

          resolve(frame);
        },
        (error) => {
          console.log(error, "❌ Socket Error!!!");
          state.stompClient.disconnect(() => {
            context.commit(mutationTypes.SOCKET_ONCLOSE_ARB);
            // context.dispatch(actionTypesSocket.soketJujConnect);
          });
        }
      );
    });
  },
  [actionTypesSocket.socketSendMessage](context, message) {
    return new Promise((resolve) => {
      state.stompClient.send("/app/send.score", JSON.stringify(message));
      console.log(JSON.stringify(message));
      context.commit(mutationTypes.SOCKET_SENDMESSAGE_JUDGIS);
      resolve();
    });
  },
  [actionTypesSocket.socketSendMessageDif](context, message) {
    return new Promise((resolve) => {
      state.stompClient.send("/app/send.dif", JSON.stringify(message));
      console.log(JSON.stringify(message));
      context.commit(mutationTypes.SOCKET_SENDMESSAGE_JUDGIS_DIF);
      resolve();
    });
  },
  [actionTypesSocket.socketSendMessageActiveUser](context, idUser) {
    return new Promise((resolve) => {
      state.stompClient.send("/app/registration.judge", JSON.stringify(idUser));
      console.log(JSON.stringify(idUser));
      context.commit(mutationTypes.SOCKET_SENDMESSAGE_ACTIVE_JUDGIS, idUser);
      resolve();
    });
  },
  [actionTypesSocket.soketJujConnect](context, user) {
    return new Promise((resolve) => {
      if (state.isConnected) return;
      console.log("Начал попытку соединения...", user);
      context.commit(mutationTypes.SOCKET_SAVE_ID_USER_JUJ, user);
      var newSoket = new SockJS("http://109.111.185.150/sport-ws");
      state.stompClient = Stomp.over(newSoket, {
        debug: false,
      });
      state.stompClient.connect(
        {},
        (frame) => {
          console.log(user.brigade, "brigadeUser");
          if (user.brigade === 1) {
            state.stompClient.subscribe("/topic/brigade_one", (tick) => {
              console.log("Подписка судьи 1 бригады");
              const itemMessageJuj = JSON.parse(tick.body);
              const parseFIO = JSON.parse(itemMessageJuj.fio);
              itemMessageJuj.fio = parseFIO;
              context.commit(
                mutationTypes.SOCKET_SPORTSMEN_JUDGINGS,
                itemMessageJuj
              );
            });
          } else {
            state.stompClient.subscribe("/topic/brigade_two", (tick) => {
              console.log("Подписка судьи 2 бригады");
              const itemMessageJuj = JSON.parse(tick.body);
              const parseFIO = JSON.parse(itemMessageJuj.fio);
              itemMessageJuj.fio = parseFIO;
              context.commit(
                mutationTypes.SOCKET_SPORTSMEN_JUDGINGS,
                itemMessageJuj
              );
            });
          }

          context.commit(mutationTypes.SOCKET_ONOPEN);
          resolve(frame);
        },
        (error) => {
          console.log(error, "❌ Socket Error!!!");
          state.stompClient.disconnect(() => {
            context.commit(mutationTypes.SOCKET_ONCLOSE);
            // context.dispatch(actionTypesSocket.soketJujConnect);
          });
        }
      );
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
