<template>
  <div class="start-list">
    <div class="wrap-loader" v-if="isLoading">
      <div id="loader"></div>
    </div>
    <h3 class="xlsx-download__title">
      Загрузить Стартовый протокол в формате XLSX
    </h3>
    <p class="xlsx-download__addon">
      *Стартовый протокол загружается в систему исключительно по сменам для
      идентификации вида соревнований!
    </p>
    <input
      type="file"
      ref="fileupload"
      @change="onChange"
      id="file"
      class="inputfile"
    />
    <label for="file">Выбрать файл</label>
    <span v-if="this.file === null">Файл не выбран</span>

    <div class="xlsx-download__input">
      <input
        type="submit"
        value="Загрузить стартовый"
        class="button btn-primary"
        @click="onSubmitProtocol()"
        :disabled="!this.file"
      />
    </div>
    <xlsx-read :file="file" v-if="this.file !== null" class="xlsx-result">
      <xlsx-table />
    </xlsx-read>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { actionTypes } from "@/store/modules/board";
import { XlsxRead, XlsxTable } from "vue-xlsx";
export default {
  name: "AppStartList",
  data() {
    return {
      file: null,
    };
  },
  computed: {
    ...mapState({
      isLoading: (state) => state.board.isLoading,
    }),
  },
  components: {
    XlsxRead,
    XlsxTable,
  },
  methods: {
    onChange(event) {
      this.file = event.target.files ? event.target.files[0] : null;
    },
    onSubmitProtocol() {
      let formData = new FormData();
      formData.append("file", this.file);
      this.$store
        .dispatch(actionTypes.postPlayers, formData)
        .then(() => {
          (this.file = null), (this.$refs.fileupload.value = null);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
