<template>
  <div class="index">
    <div class="index__title">Здравствуйте, {{ this.infoUser.fio }}!</div>
    <div
      class="index__judge"
      v-if="
        this.infoUser.brigade !== null && this.infoUser.positionNumber !== null
      "
    >
      бригада № {{ this.infoUser.brigade }} {{ this.infoUser.position }} №
      {{ this.infoUser.positionNumber }}
    </div>
    <div
      class="index__judge"
      v-if="
        this.infoUser.role === 'ADMIN' || this.infoUser.role === 'ROLE_MAIN'
      "
    >
      {{ this.infoUser.position }}
    </div>
    <div class="index__judge" v-if="this.infoUser.role === 'ROLE_ARB'">
      бригада № {{ this.infoUser.brigade }} {{ this.infoUser.position }}
    </div>

    <div class="index__text">
      Добро пожаловать в систему электронного судейства AeroSibJ.
    </div>

    <InfoBlock v-if="this.infoUser.role === 'ADMIN'" />
    <InfoBlockJujMain v-else-if="this.infoUser.role === 'ROLE_MAIN'" />
    <ChoiceCompetition :labelName="this.labelName" v-else />
    <!-- <websocket-greetings></websocket-greetings> -->
    <!-- <Sock /> -->
  </div>
</template>

<script>
import { mapState } from "vuex";
import InfoBlock from "../components/InfoBlock";
import InfoBlockJujMain from "../components/InfoBlockJujMain";
import ChoiceCompetition from "../components/ChoiceCompetition";
// import WebsocketGreetings from "@/components/WebsocketGreetings.vue";
// import Sock from "../components/Sock.vue";
export default {
  name: "index",
  data() {
    return {
      getCompetition: null,
      labelName: "Для продолжения выберите текущие соревнования",
    };
  },
  components: {
    InfoBlock,
    InfoBlockJujMain,
    ChoiceCompetition,
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      competition: (state) => state.judging.competition,
      token: (state) => state.auth.token,
    }),
  },
  mounted() {
    if (
      this.token === "undefined" ||
      this.token === "" ||
      this.infoUser === null
    ) {
      this.$router.push({ name: "Login" });
    }
  },
};
</script>

