<template>
  <div class="select-block__item">
    <div class="select-block__item-block">
      <label for="standard-select"> {{ labelName }}</label>
      <div class="select">
        <select required id="standard-select" v-model="getCompetition">
          <option
            v-for="comp in this.competition"
            :key="comp.id"
            :value="`${comp.id}`"
          >
            {{ comp.name }}
          </option>
        </select>
        <span class="focus"></span>
      </div>
    </div>
    <div class="index__btn">
      <input
        type="submit"
        value="Продолжить"
        class="button btn-primary"
        @click="procced()"
        :disabled="!this.getCompetition"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { actionTypesJudgind } from "@/store/modules/judging";
export default {
  name: "AppChoiceCompetition",
  data() {
    return {
      getCompetition: null,
    };
  },
  props: {
    labelName: String,
    // required: true,
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      competition: (state) => state.judging.competition,
      players: (state) => state.judging.players,
    }),
  },
  methods: {
    procced() {
      this.$store
        .dispatch(actionTypesJudgind.postIDCompetition, this.getCompetition)
        .then(() => {
          if (
            this.infoUser.role === "ROLE_EXE" ||
            this.infoUser.role === "ROLE_ART"
          ) {
            this.$router.push({ name: "ScoreEA" });
          } else if (this.infoUser.role === "ROLE_DIF") {
            this.$router.push({ name: "ScoreDif" });
          } else if (this.infoUser.role === "ROLE_ARB") {
            this.$router.push({ name: "Arbitr" });
          }
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
      // this.$store
      //   .dispatch(actionTypesJudgind.getNomination, this.getCompetition)
      //   .then(() => {})
      //   .catch((er) => {
      //     console.log("Ошибка", er);
      //   });
    },
  },
  mounted() {
    this.$store
      .dispatch(actionTypesJudgind.getCompetition)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>

<style></style>
