import axios from "@/api/axios";

const postScore = (credentials) => {
  return axios.post("/players/change", credentials);
};
const getScore = () => {
  return axios.get("/players");
};
const postPlayers = (players) => {
  return axios.post("/upload", players);
};
//для загрузки результатов в табло
const postScorePlayers = (players) => {
  return axios.post("/players", players);
};
export default {
  postScore,
  getScore,
  postPlayers,
  postScorePlayers,
};
