import axios from "@/api/axios";

// const getJudPlayers = () => {
//   return axios.get("/upload/sport");
// };
const getCompetition = () => {
  return axios.get("/players/competition");
};
const postIDCompetition = (id) => {
  return axios.get(`/players/competition/${id}`);
};
const setResult = (score) => {
  return axios.post(`/players/set_result`, score);
};
const getAllResults = (credentials) => {
  return axios.get(
    `/players/get/all/result/${credentials.nomination}/${credentials.brigade}`
  );
};
const getNomination = (credentials) => {
  return axios.get(
    `/players/nomination/${credentials.competition}/${credentials.brigade}`
  );
};
const getAllUsers = () => {
  return axios.get(`/players/get/all/user`);
};

export default {
  getCompetition,
  postIDCompetition,
  setResult,
  getAllResults,
  getNomination,
  getAllUsers,
};
